<script>
export default {
    data() {
        return{
            visibility: 'password'
        }
    },
    methods: {
        showPass(){
            this.visibility = 'text';
        },
        hidePass(){
            this.visibility = 'password';
        }
    }
}
</script>

<style lang="scss">
.label {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: -0.0142658px;
    color: #282929;
}
.input {
    background: #FFFFFF;
    border: 1px solid #CBCDCD;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 16px 18px;
    outline: none;
    transition:all 0.3s;
    width: 348px;
    height: 54px;
    margin-top: 6px;
    &:hover{
        border: 1px solid #505857;
    }
    &:focus{
        border: 2px solid #229A87;
    }
}
.select{
    width: 508px;
    height: 154px;
    outline: none;
    background: #FFFF;
    border: 1px solid #CBCDCD;
    border-radius: 10px;
    &_sm {
        width: 158px;
        height: 38px;
    }
    &:hover {
        border: 1px solid #505857;
    }
    &:focus {
        border: 2px solid #229A87;
    }
    &:active {
        border: 2px solid #229A87;
    }
}
.select-block {
    position: relative;
}
.search {
    position: relative;
    display: flex;
    align-items: center;
    &__input {
        background: #229A87;
        border: 1px solid #229A87;
        border-radius: 10px;
        padding: 16px 40px;
        outline: none;
        transition: all 0.3s;
        width: 342px;
        height: 38px;   
        color: #FFF;
        &::placeholder {
            color: #FFFFFF;
        }
    }
    &__icon {
        position: absolute;
        left: 13px;
        z-index: 1;
    }
}
.textarea {
    opacity: 0.2;
    border: 1px solid #36A593;
    border-radius: 10px;
    outline: none;
    padding: 26px 18px;
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.149625px;
    color: #282929;
    margin-top: 10px;
    &:hover {
        border: 1px solid #505857;
        opacity: 1;
    }
    &:active {
        border: 2px solid #229A87;
        opacity: 1;
        outline:none;
    }
    &:focus {
        border: 2px solid #229A87;
        opacity: 1;
        outline:none;
  }
}
</style>